import { useQuery } from 'react-query';
export default function useOrgUnitPathsByName(searchTerm) {
  const queryKey = [{
    orgUnits: {
      resource: 'organisationUnits',
      params: {
        fields: ['path'],
        filter: "displayName:ilike:".concat(searchTerm),
        paging: false
      }
    }
  }];
  const {
    isIdle,
    isLoading: loading,
    error,
    data
  } = useQuery(queryKey, {
    enabled: !!searchTerm,
    initialData: [],
    select: data => data.orgUnits.organisationUnits.map(_ref => {
      let {
        path
      } = _ref;
      return path;
    })
  });
  return {
    called: !isIdle,
    loading,
    error,
    data
  };
}