import { useQuery } from 'react-query';
import { useDataSetId } from '../use-context-selection/index.js';
export default function useCategoryCombination() {
  const [dataSetId] = useDataSetId();
  const queryKey = [{
    dataSet: {
      resource: 'dataSets',
      id: dataSetId,
      params: {
        fields: ['categoryCombo[isDefault,displayName, categories[id, displayName, categoryOptions[id, displayName]]]']
      }
    }
  }];
  const {
    isIdle,
    isLoading: loading,
    error,
    data
  } = useQuery(queryKey, {
    enabled: !!dataSetId,
    select: data => data.dataSet.categoryCombo
  });
  return {
    called: !isIdle,
    loading,
    error,
    data
  };
}