import { useQuery } from 'react-query';
export default function useSelectableDataSets() {
  const {
    isIdle,
    isLoading: loading,
    error,
    data
  } = useQuery([{
    dataSets: {
      resource: 'dataSets',
      params: {
        paging: false
      }
    }
  }], {
    select: data => data.dataSets.dataSets.map(dataSet => ({ ...dataSet,
      // Making sure these can be options as well
      value: dataSet.id,
      label: dataSet.displayName
    }))
  });
  return {
    called: !isIdle,
    loading,
    error,
    data
  };
}